        if (this.searchValue.politicId) {
          url += '&politicId=' + this.searchValue.politicId
        }
        if (this.searchValue.nationId) {
          url += '&nationId=' + this.searchValue.nationId
        }
        if (this.searchValue.posId) {
          url += '&posId=' + this.searchValue.posId
        }
        if (this.searchValue.jobLevelId) {
          url += '&jobLevelId=' + this.searchValue.jobLevelId
        }
        if (this.searchValue.engageForm) {
          url += '&engageForm=' + this.searchValue.engageForm
        }
        if (this.searchValue.departmentId) {
          url += '&departmentId=' + this.searchValue.departmentId
        }
        if (this.searchValue.beginDateScope) {
          url += '&beginDateScope=' + this.searchValue.beginDateScope
        }
      } else {
        url += '&name=' + this.empName
      }
      this.getRequest(url).then(resp => {
        this.loading = false
        if (resp) {
          this.emps = resp.data
          this.total = resp.total
        }
      })
    },
    currentChange (currentPage) {
      this.currentPage = currentPage
      this.initEmp()
    },
    sizeChange (size) {
      this.size = size
      this.initEmp()
    },
    prevClick (currentPage) {
      this.currentPage = currentPage--
      this.initEmp()
    },
    nextClick (currentPage) {
      this.currentPage = currentPage++
      this.initEmp()
    },
    showAddEmp () {
      this.title = 'Add Employee'
      this.emp = {
        id: null,
        name: '',
        gender: '',
        birthday: '',
        idCard: '',
        wedlock: '',
        nationId: null,
        nativePlace: '',
        politicId: null,
        email: '',
        phone: '',
        address: '',
        departmentId: null,
        jobLevelId: null,
        posId: null,
        engageForm: '',
        tiptopDegree: '',
        specialty: '',
        school: '',
        beginDate: '',
        workState: '在职',
        workID: '',
        contractTerm: null,
        conversionTime: '',
        notWorkDate: null,
        beginContract: '',
        endContract: '',
        workAge: null,
        salaryId: null
      }
      this.inputDepName = ''
      this.getWorkID()
      this.dialogVisible = true
    },
    initData () {
      if (!window.sessionStorage.getItem('nations')) {
        this.getRequest('/employee/basic/nation').then(resp => {
          if (resp) {
            this.nations = resp
            window.sessionStorage.setItem('nations', JSON.stringify(resp))
          }
        })
      } else {
        this.nations = JSON.parse(window.sessionStorage.getItem('nations'))
      }
      if (!window.sessionStorage.getItem('jobLevels')) {
        this.getRequest('/employee/basic/joblevel').then(resp => {
          if (resp) {
            this.jobLevels = resp
            window.sessionStorage.setItem('jobLevels', JSON.stringify(resp))
          }
        })
      } else {
        this.jobLevels = JSON.parse(window.sessionStorage.getItem('jobLevels'))
      }
      if (!window.sessionStorage.getItem('politicsStatus')) {
        this.getRequest('/employee/basic/politicsStatus').then(resp => {
          if (resp) {
            this.politicsStatus = resp
            window.sessionStorage.setItem('politicsStatus', JSON.stringify(resp))
          }
        })
      } else {
        this.politicsStatus = JSON.parse(window.sessionStorage.getItem('politicsStatus'))
      }
      if (!window.sessionStorage.getItem('deps')) {
        this.getRequest('/employee/basic/department').then(resp => {
          if (resp) {
            this.deps = resp
            window.sessionStorage.setItem('deps', JSON.stringify(resp))
          }
        })
      } else {
        this.deps = JSON.parse(window.sessionStorage.getItem('deps'))
      }
    },
    initPositions () {
      this.getRequest('/employee/basic/position').then(resp => {
        if (resp) {
          this.positions = resp
        }
      })
    },
    getWorkID () {
      this.getRequest('/employee/basic/maxworkid').then(resp => {
        if (resp) {
          this.emp.workID = resp.object
        }
      })
    },
    handleNodeClick (data) {
      this.inputDepName = data.name
      this.emp.departmentId = data.id
      this.visible = !this.visible
    },
    showDep () {
      this.visible = !this.visible
    },
    addEmp () {
      if (this.emp.id) {
        this.$refs.empForm.validate(valid => {
          if (valid) {
            this.putRequest('/employee/basic/', this.emp).then(resp => {
              if (resp) {
                this.dialogVisible = false
                this.initEmp()
              }
            })
          }
        })
      } else {
        this.$refs.empForm.validate(valid => {
          if (valid) {
            this.postRequest('/employee/basic/', this.emp).then(resp => {
              if (resp) {
                this.dialogVisible = false
                this.initEmp()
              }
            })
          }
        })
      }
    },
    deleteEmp (data) {
      this.$confirm('此操作将删除[' + data.name + ']的信息，是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/employee/basic/' + data.id).then(resp => {
          if (resp) {
            this.initEmp()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    showEditEmp (data) {
      this.title = 'Edit Employee'
      this.emp = data
      this.inputDepName = data.department.name
      this.dialogVisible = true
    },
    exportData () {
      this.downloadRequest('/employee/basic/export')
    },
    beforeUpload () {
      this.importBtnText = 'Importing...'
      this.importDisabled = true
      this.importBtnIcon = 'el-icon-loading'
    },
    onSuccess () {
      this.importBtnText = 'Import'
      this.importDisabled = false
      this.importBtnIcon = 'el-icon-upload2'
      this.initEmp()
    },
    onError () {
      this.importBtnText = 'Import'
      this.importDisabled = false
      this.importBtnIcon = 'el-icon-upload2'
    },
    searchHandleNodeClick (data) {
      this.inputDepName = data.name
      this.searchValue.departmentId = data.id
      this.visible2 = !this.visible2
    },
    showDepInSearch () {
      this.visible2 = !this.visible2
      if (this.visible2 === false) {
      }
    }
  }
}
</script>

<style scoped>
.single {
  margin-top: 8px;
}

.slide-fade-enter-active {
  transition: all .5s ease;
}

.slide-fade-leave-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */
{
  transform: translateX(10px);
  opacity: 0;
}
</style>
